import React, { Component } from 'react';
import PropTypes from 'prop-types';
import FormSearchBox from 'ui-library/lib/components/forms/FormSearchBox';

import { PROP_MODEL_TYPES, MODEL_TYPES } from '../../../utils/modelTypes';
import ResourceTypeContainer from '../../../containers/ResourceTypeContainer/ResourceTypeContainer';

import './SearchBar.css';

class SearchBar extends Component {
  constructor(props) {
    super(props);

    this.searchbox = React.createRef();

    this.handleSearchChange = this.handleSearchChange.bind(this);
    this.handleSearchClear = this.handleSearchClear.bind(this);
    this.handleKeyDown = this.handleKeyDown.bind(this);
    this.handleFilterSearch = this.handleFilterSearch.bind(this);

    this.state = {
      queryString: props.persistQueryString ? props.queryString : '',
    };
  }

  handleKeyDown(event) {
    if (event.key === 'Enter' && !this.props.isDisabled) {
      this.props.onSearch(this.state.queryString);
    }
  }

  handleSearchChange(queryString) {
    if (!this.props.isDisabled) {
      this.setState({ queryString });
    }
  }

  handleSearchClear() {
    this.setState({ queryString: '' });
  }

  handleFilterSearch() {
    const { pageModel } = this.props;
    this.handleSearchClear();
    this.props.onClearSearch();
    if (pageModel === MODEL_TYPES.GROUPS) this.props.onSearch('', { skipMessage: true });
    this.searchbox.current.searchBoxFocus();
  }

  render() {
    const { showDropDown, primaryDropDown } = this.props;
    const classes = `SearchBar ${this.props.className}`;

    return (
      <div className={classes}>
        {
          showDropDown &&
          <ResourceTypeContainer
            primary={primaryDropDown}
            onFilter={this.handleFilterSearch}
          />
        }
        <FormSearchBox
          flags={['p-stateful']}
          ref={this.searchbox}
          placeholder={this.props.placeholder}
          className="SearchBox"
          onClear={this.handleSearchClear}
          onKeyDown={this.handleKeyDown}
          queryString={this.state.queryString}
          onValueChange={this.handleSearchChange}
        />
      </div>
    );
  }
}

SearchBar.propTypes = {
  className: PropTypes.string,
  onClearSearch: PropTypes.func.isRequired,
  onSearch: PropTypes.func.isRequired,
  placeholder: PropTypes.string,
  queryString: PropTypes.string,
  persistQueryString: PropTypes.bool,
  isDisabled: PropTypes.bool,
  showDropDown: PropTypes.bool,
  primaryDropDown: PropTypes.bool,
  pageModel: PropTypes.oneOf(PROP_MODEL_TYPES).isRequired,
};

SearchBar.defaultProps = {
  className: '',
  placeholder: '',
  queryString: '',
  persistQueryString: false,
  isDisabled: false,
  showDropDown: false,
  primaryDropDown: true,
};

export default SearchBar;
