import React from 'react';
import PropTypes from 'prop-types';
import Modal from 'ui-library/lib/components/general/Modal';
import UILibLink from 'ui-library/lib/components/general/Link';

export const AlertModal = ({
  isOpen,
  modalTitle,
  modalDescription,
  onCancel,
  cancelText,
  onSave,
  saveText,
  onClose,
  closeText,
  preventSave,
}) => {
  if (isOpen) {
    return (
      <div className="modal-container">
        <Modal
          flags={['use-portal']}
          type="alert"
          expanded={isOpen}
        >
          {
            modalTitle &&
            <div className="title">
              {modalTitle}
            </div>
          }

          {
            modalDescription &&
            <p>
              {modalDescription}
            </p>
          }

          <div className="buttons">
            {
              onCancel &&
              <button className="cancel" onClick={onCancel}>
                {cancelText}
              </button>
            }

            <button className="primary" onClick={onSave} disabled={preventSave}>
              {saveText}
            </button>

            <br />
            <br />

            {
              onClose &&
              <UILibLink
                type="block"
                title={closeText}
                className="close"
                onClick={onClose}
                onKeyPress={onClose}
              />
            }
          </div>
        </Modal>
      </div>
    );
  }

  return null;
};

AlertModal.propTypes = {
  isOpen: PropTypes.bool,
  modalTitle: PropTypes.string,
  modalDescription: PropTypes.string,
  onCancel: PropTypes.func,
  cancelText: PropTypes.string,
  onSave: PropTypes.func.isRequired,
  saveText: PropTypes.string,
  onClose: PropTypes.func,
  closeText: PropTypes.string,
  preventSave: PropTypes.bool,
};

AlertModal.defaultProps = {
  isOpen: false,
  modalTitle: '',
  modalDescription: '',
  onCancel: undefined,
  cancelText: '',
  saveText: '',
  onClose: undefined,
  closeText: '',
  preventSave: false,
};

export default AlertModal;
