import React from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { useIntl } from 'react-intl';
import Spinner from 'ui-library/lib/components/general/Spinner';
import Section from 'ui-library/lib/components/general/Section';

import ResetPasswordField from '../ResetPasswordField/ResetPasswordField';
import EditableFieldV2 from '../EditableFieldV2/EditableFieldV2';
import {
  categorizeAttributes,
  getNonBlacklistedAttributes,
} from '../../../../utils/helpers';

const FormFields = (props) => {
  const {
    values,
    touched,
    errors,
    status,
    handleBlur,
    handleChange,
    setFieldValue,
    setFieldTouched,
    setStatus,

    meta,
    selected,
    serverErrors,
  } = props;
  const intl = useIntl();
  const categories = meta.attributeCategories;
  let fields;

  const handleCustomValidationChange = (attribute, isValid) => {
    setStatus({
      ...status,
      customValidation: {
        ...status.customValidation,
        [attribute]: isValid,
      },
    });
  };

  const mapFields = (attr) => {
    const errorMessage = touched[attr] ? errors[attr] : '';
    const attrMeta = _.get(meta, `attributes.${attr}`);
    let serverError;

    if (!touched[attr] && serverErrors[attr]) serverError = serverErrors[attr].message;

    if (attr === 'userPassword') {
      return (
        <div className="input-row" key={attr}>
          <ResetPasswordField
            showHelpHint
            onBlur={handleBlur}
            onChange={handleChange}
            setFieldValue={setFieldValue}
            setFieldTouched={setFieldTouched}
            password={values[attr]}
            errorMessage={errorMessage}
            serverError={serverError}
          />
        </div>
      );
    }

    if (!attrMeta) {
      return <Spinner show key={attr} />;
    }

    return (
      <div className="input-row" key={attr}>
        <EditableFieldV2
          attr={attr}
          meta={attrMeta}
          value={values[attr]}
          values={values}
          errorMessage={errorMessage}
          serverError={serverError}
          resourceType={selected}
          onBlur={handleBlur}
          onChange={handleChange}
          onValueChange={setFieldValue}
          onValidationChange={handleCustomValidationChange}
          setFieldTouched={setFieldTouched}
        />
      </div>
    );
  };

  if (categories && categories.length) {
    const attributes = getNonBlacklistedAttributes(meta.attributes, values);
    const categorizedAttrs = categorizeAttributes(categories, attributes, intl);
    let passwordField;

    if ('userPassword' in values) {
      passwordField = mapFields('userPassword');
    }

    const categorizedFields = categorizedAttrs.map(category => (
      <div className="attribute-category" key={category.name}>
        <Section
          title={category.name}
          initialState={{
            expanded: true,
          }}
        >
          {category.attributes.map(attr => mapFields(attr.name))}
        </Section>
      </div>
    ));

    fields = passwordField ? [passwordField, ...categorizedFields] : categorizedFields;
  } else {
    fields = Object.keys(values).map(mapFields);
  }

  return <>{fields}</>;
};

FormFields.propTypes = {
  values: PropTypes.objectOf(PropTypes.any).isRequired,
  touched: PropTypes.objectOf(PropTypes.bool).isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  status: PropTypes.shape({
    customValidation: PropTypes.objectOf(PropTypes.bool).isRequired,
  }).isRequired,
  handleBlur: PropTypes.func.isRequired,
  handleChange: PropTypes.func.isRequired,
  setFieldValue: PropTypes.func.isRequired,
  setFieldTouched: PropTypes.func.isRequired,
  setStatus: PropTypes.func.isRequired,

  meta: PropTypes.shape({
    attributeCategories: PropTypes.arrayOf(PropTypes.string),
    attributes: PropTypes.shape({}).isRequired,
  }).isRequired,
  selected: PropTypes.shape({
    id: PropTypes.string.isRequired,
  }).isRequired,
  serverErrors: PropTypes.objectOf(PropTypes.objectOf(PropTypes.string)).isRequired,
};

export default FormFields;
