import { SUCCESS, FAILURE, REQUEST } from '../../actions';
import {
  RESOURCE_TYPES,
  RESOURCE_TYPES_FILTER,
  SELECT_RESOURCE_TYPE,
  SELECT_SECONDARY_RESOURCE_TYPE,
  PARENT_RESOURCES,
} from '../../actions/actionTypes';

export const initialState = {
  data: undefined,
  links: undefined,
  selected: undefined,
  secondarySelected: undefined,
  selectedParentResource: {
    data: undefined,
    meta: undefined,
    error: undefined,
    isRequesting: false,
  },
  filter: undefined,
  error: undefined,
};

export default function (state = initialState, action) {
  switch (action.type) {
    case RESOURCE_TYPES[SUCCESS]:
      return reduceResourceTypesSuccess(action.data, state);
    case RESOURCE_TYPES[FAILURE]:
      return reduceResourceTypesFailure(action.error, state);
    case RESOURCE_TYPES_FILTER:
      return reduceResourceTypesFilter(action.filter, state);
    case SELECT_RESOURCE_TYPE:
      return reduceResourceTypesSelection(action.selected, state);
    case SELECT_SECONDARY_RESOURCE_TYPE:
      return reduceResourceTypesSelectionSecondary(action.selected, state);
    case PARENT_RESOURCES[REQUEST]:
      return reduceParentResourceTypeRequest(state);
    case PARENT_RESOURCES[SUCCESS]:
      return reduceParentResourceTypeSuccess(action.data, state);
    case PARENT_RESOURCES[FAILURE]:
      return reduceParentResourceTypeFailure(action.error, state);
    default:
      return state;
  }
}

function reduceResourceTypesSuccess(data, prevState) {
  return {
    ...prevState,
    ...data,
    filter: undefined,
    error: undefined,
  };
}

function reduceResourceTypesFailure(error, prevState) {
  return {
    ...prevState,
    data: undefined,
    links: undefined,
    error,
  };
}

function reduceResourceTypesSelection(selected, prevState) {
  return {
    ...prevState,
    selectedParentResource: undefined,
    selected,
  };
}

function reduceResourceTypesSelectionSecondary(selected, prevState) {
  return {
    ...prevState,
    secondarySelected: selected,
  };
}

function reduceParentResourceTypeRequest(prevState) {
  return {
    ...prevState,
    selectedParentResource: {
      ...prevState.selectedParentResource,
      isRequesting: true,
    },
  };
}

function reduceParentResourceTypeSuccess(data, prevState) {
  return {
    ...prevState,
    selectedParentResource: {
      ...prevState.selectedParentResource,
      ...data,
      error: undefined,
      isRequesting: false,
    },
  };
}

function reduceParentResourceTypeFailure(error, prevState) {
  return {
    ...prevState,
    selectedParentResource: {
      ...prevState.selectedParentResource,
      data: undefined,
      meta: undefined,
      error,
      isRequesting: false,
    },
  };
}

function reduceResourceTypesFilter(filter, prevState) {
  return {
    ...prevState,
    filter,
  };
}
