import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import _ from 'lodash';

import Permission from '../../shared/layout/Permission/Permission';
import NewModelPage from '../../shared/pages/NewModelPage/NewModelPage';
import { createUserAction } from '../../../store/actions/userActions';
import { MODEL_TYPES } from '../../../utils/modelTypes';
import { RESOURCE_TYPES } from '../../../utils/resourceTypes';
import { PERMISSIONS } from '../../../utils/permissions/permissions';

export const NewUserPage = ({
  createAction,
}) => {
  const resourceType = RESOURCE_TYPES.USER;
  return (
    <Permission
      requiredPermissions={[PERMISSIONS.CREATE]}
      resourceType={resourceType}
    >
      <NewModelPage
        title={
          <FormattedMessage
            id="page.new-model.page-header"
            values={{
              resourceType: _.capitalize(resourceType),
            }}
          />
        }
        modelType={MODEL_TYPES.USERS}
        resourceType={resourceType}
        createAction={createAction}
      />
    </Permission>
  );
};

NewUserPage.propTypes = {
  createAction: PropTypes.func.isRequired,
};

export default connect(
  undefined,
  {
    createAction: createUserAction,
  },
)(NewUserPage);
