import React from 'react';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import _ from 'lodash';

import Permission from '../../shared/layout/Permission/Permission';
import NewModelPage from '../../shared/pages/NewModelPage/NewModelPage';
import { MODEL_TYPES } from '../../../utils/modelTypes';
import { RESOURCE_TYPES, getResourceById } from '../../../utils/resourceTypes';
import { PERMISSIONS } from '../../../utils/permissions/permissions';

const GenericCreatePage = ({
  createAction,
  match,
}) => {
  const resourceType = RESOURCE_TYPES.GENERIC;
  const resourceId = match.params.type;
  const resource = getResourceById(resourceId);
  return (
    <Permission
      requiredPermissions={[PERMISSIONS.CREATE]}
      resourceType={resourceType}
    >
      <NewModelPage
        title={
          <FormattedMessage
            id="page.new-model.page-header"
            values={{
              resourceType: _.get(resource, 'attributes.displayName'),
            }}
          />
        }
        modelType={MODEL_TYPES.GENERICS}
        resourceType={resourceType}
        createAction={createAction}
        match={match}
      />
    </Permission>
  );
};

GenericCreatePage.propTypes = {
  createAction: PropTypes.func.isRequired,
  match: PropTypes.shape({
    params: PropTypes.shape({
      type: PropTypes.string,
    }).isRequired,
  }).isRequired,
};

export default GenericCreatePage;
