import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import _ from 'lodash';
import InlineMessage, { MessageTypes } from 'ui-library/lib/components/general/InlineMessage';

import ColumnList from '../ColumnList/ColumnList';
import { MODEL_TYPES, PROP_MODEL_TYPES } from '../../../../utils/modelTypes';

export class Column extends Component {
  constructor(props) {
    super(props);

    this.getColumnListItems = this.getColumnListItems.bind(this);
    this.getColumnMessage = this.getColumnMessage.bind(this);
  }

  getColumnListItems() {
    const { isAddable, membershipResults, model } = this.props;

    if (isAddable && _.get(membershipResults, 'nonMembers.length', 0)) {
      return membershipResults.nonMembers;
    } else if (!isAddable && _.get(membershipResults, 'members.length', 0)) {
      return membershipResults.members;
    } else if (!isAddable && _.get(model, 'included.length', 0) && !membershipResults) {
      return model.included;
    }

    return undefined;
  }

  getColumnMessage(items) {
    const {
      isAddable,
      membershipResults,
      profilePageModel,
      modelDisplayAttributeValue,
      secondarySelected,
      filteredResourceTypes,
    } = this.props;

    const nonMembersExceeded = isAddable && _.get(membershipResults, 'meta.nonMembers.sizeLimitExceeded', false);
    const membersExceeded = !isAddable && (
      _.get(membershipResults, 'meta.directMembers.sizeLimitExceeded', false) ||
      _.get(membershipResults, 'meta.indirectMembers.sizeLimitExceeded', false)
    );

    const isUnsearched = isAddable && !membershipResults;
    const isEmptySearch = !items && membershipResults && membershipResults.queryString.length;
    const isEmptyList = !items;

    if (nonMembersExceeded) {
      return {
        type: MessageTypes.ERROR,
        intlKey: `components.dual-column-search.${profilePageModel}.column.size-limit-exceeded`,
        values: {
          queryString: membershipResults.queryString,
        },
      };
    } else if (membersExceeded) {
      return {
        type: MessageTypes.ERROR,
        intlKey: `components.dual-column-search.${profilePageModel}.column.result-too-large`,
      };
    } else if (isUnsearched) {
      return {
        type: MessageTypes.NOTICE,
        intlKey: `components.dual-column-search.${profilePageModel}.column.enter-search-prompt`,
      };
    } else if (isEmptySearch) {
      return {
        type: MessageTypes.ERROR,
        intlKey: `components.dual-column-search.${profilePageModel}.column.empty-search`,
        values: {
          queryString: membershipResults.queryString,
        },
      };
    } else if (isEmptyList && isAddable) {
      let postfix = '';

      if (profilePageModel !== MODEL_TYPES.GROUPS) {
        postfix = filteredResourceTypes.length > 1 ? '.other' : '.one';
      }

      return {
        type: MessageTypes.NOTICE,
        intlKey: `components.dual-column-search.${profilePageModel}.column.empty-nonmembers-list${postfix}`,
        values: {
          displayName: modelDisplayAttributeValue,
          selectedResource: _.get(secondarySelected, 'attributes.displayName'),
        },
      };
    } else if (isEmptyList && !isAddable) {
      let postfix = '';

      if (profilePageModel !== MODEL_TYPES.GROUPS) {
        postfix = filteredResourceTypes.length > 1 ? '.other' : '.one';
      }

      return {
        type: MessageTypes.NOTICE,
        intlKey: `components.dual-column-search.${profilePageModel}.column.empty-members-list${postfix}`,
        values: {
          displayName: modelDisplayAttributeValue,
          selectedResource: _.get(secondarySelected, 'attributes.displayName'),
        },
      };
    }

    return undefined;
  }

  render() {
    const {
      isAddable,
      profilePageModel,
      modelMeta,
      model,
    } = this.props;
    const columnNumber = isAddable ? 1 : 2;
    const columnListItems = this.getColumnListItems();
    const columnMessage = this.getColumnMessage(columnListItems);

    return (
      <div className="Column">
        <div className="column-header">
          <span className="column-title">
            <FormattedMessage
              id={`components.dual-column-search.${profilePageModel}.column.title.column-${columnNumber}`}
            />
          </span>
        </div>
        {
          columnMessage &&
          <InlineMessage
            className="column-message"
            type={columnMessage.type}
          >
            <FormattedMessage
              id={columnMessage.intlKey}
              values={columnMessage.values}
            />
          </InlineMessage>
        }
        {
          columnListItems &&
          <ColumnList
            profilePageModel={profilePageModel}
            isAddable={isAddable}
            items={columnListItems}
            modelMeta={modelMeta}
            model={model}
          />
        }
      </div>
    );
  }
}

Column.propTypes = {
  isAddable: PropTypes.bool,
  profilePageModel: PropTypes.oneOf(PROP_MODEL_TYPES).isRequired,
  model: PropTypes.shape({
    id: PropTypes.string.isRequired,
    attributes: PropTypes.shape({}).isRequired,
    included: PropTypes.arrayOf(PropTypes.shape({})),
  }),
  modelMeta: PropTypes.shape({
    attributes: PropTypes.shape({}).isRequired,
    included: PropTypes.shape({}),
  }),
  modelDisplayAttributeValue: PropTypes.string,
  membershipResults: PropTypes.shape({
    queryString: PropTypes.string.isRequired,
    members: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    nonMembers: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    meta: PropTypes.shape({
      directMembers: PropTypes.shape({}),
      indirectMembers: PropTypes.shape({}),
      nonMembers: PropTypes.shape({}),
    }).isRequired,
    error: PropTypes.string,
    groupsError: PropTypes.bool,
  }),
  secondarySelected: PropTypes.shape({
    id: PropTypes.string.isRequired,
    attributes: PropTypes.shape({
      displayName: PropTypes.string,
    }).isRequired,
  }).isRequired,
  filteredResourceTypes: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.string.isRequired,
    attributes: PropTypes.shape({
      displayName: PropTypes.string.isRequired,
    }).isRequired,
  })),
};

Column.defaultProps = {
  isAddable: false,
  membershipResults: undefined,
  model: undefined,
  modelMeta: undefined,
  modelDisplayAttributeValue: undefined,
  filteredResourceTypes: [],
};

function mapStateToProps(state) {
  const { secondarySelected, filter } = state.resourceTypes;

  return {
    secondarySelected,
    filteredResourceTypes: filter && filter.data,
  };
}

export default connect(mapStateToProps)(Column);
