import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import Icon from 'ui-library/lib/components/general/Icon';
import HelpHint from 'ui-library/lib/components/tooltips/HelpHint';

import ModifyMemberButton from '../ModifyMemberButton/ModifyMemberButton';
import ModelSummary from '../../summaries/ModelSummary/ModelSummary';
import { PROP_MODEL_TYPES, MODEL_TYPES } from '../../../../utils/modelTypes';
import { getResourceById, iconFor } from '../../../../utils/resourceTypes';
import { blacklistedDisplayAttrs } from '../../../../utils/helpers';

import './ColumnListItem.css';

class ColumnListItem extends Component {
  constructor(props) {
    super(props);

    this.itemContent = this.itemContent.bind(this);
    this.tooltipContent = this.tooltipContent.bind(this);
    this.displayTooltip = this.displayTooltip.bind(this);
  }

  tooltipContent() {
    const {
      item,
      modelMeta,
      model,
    } = this.props;
    const includedMeta =
      item.type === model.type
        ? modelMeta
        : _.get(modelMeta, `included[${item.type}].attributes`);

    return (
      <ModelSummary
        attributes={item.attributes}
        id={item.id}
        meta={includedMeta}
        showCategories={false}
      />
    );
  }

  itemContent() {
    const {
      item,
      itemName,
      isAddable,
      model,
      profilePageModel,
    } = this.props;
    const { resourceType } = getResourceById(item.type).attributes;
    const iconName = iconFor(resourceType);
    const target = profilePageModel === MODEL_TYPES.GROUPS ? item : model;
    const container = profilePageModel === MODEL_TYPES.GROUPS ? model : item;

    return (
      <div className="item-row">
        <div className="item-row__labels">
          <div className="item-row__name">
            <Icon
              iconName={iconName}
              type="leading"
            />
            {itemName}
          </div>
        </div>
        <ModifyMemberButton
          isAddable={isAddable}
          target={target}
          container={container}
          profilePageModel={profilePageModel}
        />
      </div>
    );
  }

  displayTooltip() {
    return (
      <HelpHint
        placement="top"
        hintText={this.tooltipContent()}
        delayShow={400}
      >
        {this.itemContent()}
      </HelpHint>
    );
  }

  render() {
    const { item } = this.props;
    const displayAttrs =
      Object
        .keys(item.attributes)
        .filter(attr => blacklistedDisplayAttrs.indexOf(attr) < 0);

    return (
      <Fragment>
        <li
          key={item.id}
          className="ColumnListItem"
          onMouseEnter={this.debouncedOpen}
          onMouseLeave={this.handleClose}
        >
          {
            displayAttrs.length > 1
              ? this.displayTooltip()
              : this.itemContent()
          }
        </li>
      </Fragment>
    );
  }
}

ColumnListItem.propTypes = {
  isAddable: PropTypes.bool,
  model: PropTypes.shape({
    id: PropTypes.string.isRequired,
    attributes: PropTypes.shape({}).isRequired,
    included: PropTypes.arrayOf(PropTypes.shape({})),
  }),
  item: PropTypes.shape({}).isRequired,
  itemName: PropTypes.string.isRequired,
  profilePageModel: PropTypes.oneOf(PROP_MODEL_TYPES).isRequired,
  modelMeta: PropTypes.shape({
    attributes: PropTypes.shape({}).isRequired,
  }).isRequired,
};

ColumnListItem.defaultProps = {
  isAddable: false,
  model: undefined,
};

export default ColumnListItem;
