import React, { Component } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';

import ColumnListItem from '../ColumnListItem/ColumnListItem';
import { getTitleOrPrimaryAttributeValue } from '../../../../utils/helpers';
import { getResourceById } from '../../../../utils/resourceTypes';
import { PROP_MODEL_TYPES } from '../../../../utils/modelTypes';

import './ColumnList.css';

class ColumnList extends Component {
  constructor(props) {
    super(props);

    this.displayItems = this.displayItems.bind(this);
    this.handleScroll = this.handleScroll.bind(this);

    this.state = {
      limit: 100,
    };
  }

  handleScroll(e) {
    const { items } = this.props;
    const { limit } = this.state;
    const bottom = e.target.scrollHeight - e.target.scrollTop === e.target.clientHeight;
    if (bottom && limit <= items.length) {
      this.setState({ limit: limit + 100 });
    }
  }

  displayItems() {
    const {
      isAddable,
      items,
      modelMeta,
      model,
      profilePageModel,
    } = this.props;
    const { limit } = this.state;

    if (items.length) {
      return (
        <ul className="items-list">
          {
            items
              .slice(0, limit)
              .filter((item) => {
                const resourceType = _.get(getResourceById(item.type), 'attributes.resourceType');
                // Don't add the item to the result if there is no resourceType found for it
                return !!resourceType;
              })
              .map((item) => {
                /**
                 * Determine whether it's a group of the same type (not included) because only
                 * groups can display other groups in their searches currently.
                 */
                const attributeMeta =
                  item.type === model.type
                    ? modelMeta
                    : _.get(modelMeta, `included[${item.type}].attributes`);

                const itemName = getTitleOrPrimaryAttributeValue(
                  item.attributes,
                  attributeMeta,
                );

                return (
                  <ColumnListItem
                    key={item.id}
                    isAddable={isAddable}
                    item={item}
                    itemName={itemName}
                    model={model}
                    profilePageModel={profilePageModel}
                    modelMeta={modelMeta}
                  />
                );
              })
          }
        </ul>
      );
    }

    return null;
  }

  render() {
    const { isAddable } = this.props;
    const isAddableClass = `${isAddable ? 'addable' : 'added'}`;

    return (
      <div className={`ColumnList ${isAddableClass}`} onScroll={this.handleScroll}>
        {this.displayItems()}
      </div>
    );
  }
}

ColumnList.propTypes = {
  isAddable: PropTypes.bool,
  items: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  modelMeta: PropTypes.shape({
    attributes: PropTypes.shape({}).isRequired,
  }),
  model: PropTypes.shape({
    id: PropTypes.string.isRequired,
    attributes: PropTypes.shape({}).isRequired,
    included: PropTypes.arrayOf(PropTypes.shape({})),
  }),
  profilePageModel: PropTypes.oneOf(PROP_MODEL_TYPES).isRequired,
};

ColumnList.defaultProps = {
  isAddable: false,
  model: undefined,
  modelMeta: undefined,
};

export default ColumnList;
