import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useSelector, useDispatch } from 'react-redux';
import { withFormik } from 'formik';
import { useIntl } from 'react-intl';
import { useHistory } from 'react-router-dom';
import ButtonBar from 'ui-library/lib/components/forms/ButtonBar';
import { selectItem } from 'ui-library/lib/components/panels/left-nav/Actions';

import './ModelFormV2.css';
import FormFields from '../FormFields/FormFields';
import AlertModal from '../../modals/AlertModal/AlertModal';
import { generateModelFormValues, validateModelForm } from '../../../../utils/helpers';
import { setModelFormAction } from '../../../../store/actions/modelForm/modelFormActions';

const ModelFormV2 = (props) => {
  const {
    // Props from withFormik HOC
    dirty,
    values,
    touched,
    errors,
    status,
    handleChange,
    submitForm,
    setFieldValue,
    setFieldTouched,
    setStatus,
    handleBlur,
    handleReset,
    isSubmitting,
    isValid,
    resetForm,

    // Passed in props
    meta,
    selected,
  } = props;
  const { attributeErrors, isDirty } = useSelector(state => state.modelForm);
  const history = useHistory();
  const intl = useIntl();
  const [nextLocation, setNextLocation] = useState();
  const [modalOpen, setModalOpen] = useState(false);
  const dispatch = useDispatch();

  // Set field back to untouched when it has a server error so we can display a server error only
  // when it hasn't been touched
  useEffect(() => {
    Object.keys(attributeErrors).forEach(key => setFieldTouched(key, false));
  }, [attributeErrors, setFieldTouched]);

  // Clear any server-generated attribute errors on unmounting
  useEffect(() => () => dispatch(setModelFormAction({ attributeErrors: {} })), [dispatch]);

  // Update Redux modelForm isDirty state (for LeftNav transition prevention)
  useEffect(() => {
    if (isDirty !== dirty) dispatch(setModelFormAction({ isDirty: dirty }));
  }, [dispatch, dirty, isDirty]);

  // Prevent and capture URL transition when form is dirty
  useEffect(() => {
    const unblock = history.block((next) => {
      if (dirty) {
        setModalOpen(true);
        setNextLocation(next);
      }
      return !dirty;
    });

    return () => unblock();
  }, [history, dirty]);

  const handleModalCancel = () => {
    setModalOpen(false);
    setNextLocation(undefined);
  };
  const handleModalSave = () => {
    setModalOpen(false);
    submitForm();
  };
  const handleModalReset = async () => {
    await resetForm();
    // Update URL and select the left nav item associated with the next location change
    if (nextLocation && nextLocation.pathname) {
      history.replace(nextLocation.pathname);
      selectItem(nextLocation.pathname);
    }
  };

  // Check that all custom fields have a value of true to indicate they are valid
  const allCustomFieldsValid = Object.values(status.customValidation).every(item => item);
  const isFullyValid = isValid && allCustomFieldsValid;

  return (
    <form className="ModelForm">
      <AlertModal
        isOpen={modalOpen}
        modalTitle={intl.formatMessage({ id: 'containers.model-form.alert-modal.title' })}
        modalDescription={intl.formatMessage({ id: 'containers.model-form.alert-modal.message' })}
        onCancel={handleModalReset}
        cancelText={intl.formatMessage({ id: 'common.discard-changes' })}
        onSave={handleModalSave}
        saveText={intl.formatMessage({ id: 'common.save' })}
        onClose={handleModalCancel}
        closeText={intl.formatMessage({ id: 'common.cancel' })}
        preventSave={!isFullyValid}
      />

      <FormFields
        values={values}
        touched={touched}
        errors={errors}
        status={status}
        handleBlur={handleBlur}
        handleChange={handleChange}
        setFieldValue={setFieldValue}
        setFieldTouched={setFieldTouched}
        setStatus={setStatus}
        meta={meta}
        selected={selected}
        serverErrors={attributeErrors}
      />

      <ButtonBar
        flags={['use-portal']}
        visible={Object.keys(touched).length > 0}
        cancelText={intl.formatMessage({ id: 'common.reset' })}
        saveText={intl.formatMessage({ id: 'common.save' })}
        saveDisabled={!isFullyValid}
        enableSavingAnimation={isSubmitting}
        onCancel={handleReset}
        onSave={submitForm}
      />
    </form>
  );
};

ModelFormV2.propTypes = {
  // Props from withFormik HOC
  dirty: PropTypes.bool.isRequired,
  values: PropTypes.objectOf(PropTypes.any).isRequired,
  touched: PropTypes.objectOf(PropTypes.bool).isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  status: PropTypes.shape({
    customValidation: PropTypes.objectOf(PropTypes.bool).isRequired,
  }).isRequired,
  handleChange: PropTypes.func.isRequired,
  submitForm: PropTypes.func.isRequired,
  setFieldValue: PropTypes.func.isRequired,
  setFieldTouched: PropTypes.func.isRequired,
  setStatus: PropTypes.func.isRequired,
  handleBlur: PropTypes.func.isRequired,
  handleReset: PropTypes.func.isRequired,
  isSubmitting: PropTypes.bool.isRequired,
  isValid: PropTypes.bool.isRequired,
  resetForm: PropTypes.func.isRequired,

  // Passed in props
  meta: PropTypes.shape({
    attributeCategories: PropTypes.arrayOf(PropTypes.string),
    attributes: PropTypes.shape({}).isRequired,
  }).isRequired,
  model: PropTypes.shape({
    id: PropTypes.string,
    type: PropTypes.string,
    attributes: PropTypes.object,
    included: PropTypes.arrayOf(PropTypes.object),
  }),
  selected: PropTypes.shape({
    id: PropTypes.string.isRequired,
  }).isRequired,
  // onSave needed in formikProps
  // eslint-disable-next-line react/no-unused-prop-types
  onSave: PropTypes.func.isRequired,
};

const defaultProps = {
  model: {
    id: '',
    type: '',
    attributes: {},
    included: [],
  },
};

ModelFormV2.defaultProps = defaultProps;

const formikProps = {
  displayName: 'ModelFormV2',
  enableReinitialize: true,
  mapPropsToValues: generateModelFormValues,
  mapPropsToStatus: (props) => {
    const { attributes } = props.meta;
    const customValidation = Object.entries(attributes).reduce((result, [key, val]) => {
      if (val.customPresentation) return { ...result, [key]: true };
      return result;
    }, {});

    return { customValidation };
  },
  validate: (values, props) => validateModelForm(props, values),
  handleSubmit: async (values, { props, setSubmitting }) => {
    const initialValues = generateModelFormValues(props);
    const changedValues = Object
      .entries(values)
      .reduce((result, [key, value]) => {
        if (initialValues[key] === value) return result;

        return { ...result, [key]: [value] };
      }, {});

    await props.onSave(changedValues);
    setSubmitting(false);
  },
};

export default withFormik(formikProps)(ModelFormV2);
