import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import { useIntl } from 'react-intl';
import FormCheckbox from 'ui-library/lib/components/forms/FormCheckbox';
import FormTextField from 'ui-library/lib/components/forms/form-text-field';
import FormIntegerField from 'ui-library/lib/components/forms/form-integer-field';
import Calendar from 'ui-library/lib/components/calendars/Calendar';

import CustomAttributeFieldV2 from '../CustomAttributeFieldV2/CustomAttributeFieldV2';

export const FIELD_TYPES = {
  STRING: 'string',
  INTEGER: 'integer',
  BOOLEAN: 'boolean',
  DATETIME: 'dateTime',
};

const EditableFieldV2 = (props) => {
  const {
    attr,
    attributeErrors,
    errorMessage,
    serverError,
    value,
    values,
    resourceType,
    meta,
    onBlur,
    onChange,
    setFieldTouched,
    onValueChange,
    onValidationChange,
  } = props;
  const {
    displayName,
    readOnly,
    dataType,
    required,
    customPresentation,
  } = meta;
  const intl = useIntl();

  const handleChange = (event) => {
    setFieldTouched(attr);
    onChange(event);
  };

  const handleCustomChange = (newValue) => {
    setFieldTouched(attr);
    onValueChange(attr, newValue);
  };

  const handleIntegerChange = (newValue) => {
    if (newValue === '') {
      setFieldTouched(attr);
      onValueChange(attr, newValue);
      return;
    }

    const parsedValue = newValue === '-' ? newValue : parseInt(newValue, 10);

    if (!FormIntegerField.isValid(newValue) && newValue !== '-') {
      return;
    }

    setFieldTouched(attr);
    onValueChange(attr, parsedValue);
  };

  const handleDateChange = (newDate) => {
    setFieldTouched(attr);
    onValueChange(attr, moment(newDate, 'MM-DD-YYYY').toISOString());
  };

  const handleValidationChange = (isValid) => {
    onValidationChange(attr, isValid);
  };

  const renderField = () => {
    let error = errorMessage ? intl.formatMessage({ id: errorMessage }) : '';
    error = serverError || error;

    if (customPresentation) {
      return (
        <div className="CustomField">
          <CustomAttributeFieldV2
            attr={attr}
            value={value}
            values={values}
            resourceType={resourceType}
            onValueChange={handleCustomChange}
            onValidationChange={handleValidationChange}
            meta={meta}
            attributeErrors={attributeErrors}
          />
        </div>
      );
    } else if (dataType === FIELD_TYPES.STRING || (readOnly && dataType === FIELD_TYPES.DATETIME)) {
      return (
        <div className="TextField">
          <FormTextField
            name={attr}
            flags={['p-stateful']}
            labelText={displayName}
            value={value || ''}
            onBlur={onBlur}
            onChange={handleChange}
            readOnly={readOnly}
            errorMessage={error}
            required={required}
          />
        </div>
      );
    } else if (dataType === FIELD_TYPES.INTEGER) {
      return (
        <div className="IntegerField">
          <FormIntegerField
            name={attr}
            labelText={displayName}
            value={value}
            onBlur={onBlur}
            onValueChange={handleIntegerChange}
            readOnly={readOnly}
            errorMessage={error}
            required={required}
            min={-999999999999999}
          />
        </div>
      );
    } else if (dataType === FIELD_TYPES.BOOLEAN) {
      return (
        <div className="Checkbox">
          <FormCheckbox
            name={attr}
            className="inline"
            label={displayName}
            checked={value}
            onBlur={onBlur}
            onChange={handleChange}
            disabled={readOnly}
            errorMessage={error}
          />
        </div>
      );
    } else if (dataType === FIELD_TYPES.DATETIME) {
      return (
        <div className="DateTime">
          <Calendar
            name={attr}
            flags={['p-stateful', 'use-portal']}
            closeOnSelect
            date={value}
            required={required}
            label={displayName}
            onBlur={onBlur}
            onValueChange={handleDateChange}
            errorMessage={error}
          />
        </div>
      );
    }

    return null;
  };

  const field = renderField();
  if (!field) return null;

  return (
    <div className="EditableFieldV2">
      {field}
    </div>
  );
};

EditableFieldV2.propTypes = {
  attr: PropTypes.string.isRequired,
  attributeErrors: PropTypes.objectOf(
    PropTypes.shape({
      message: PropTypes.string.isRequired,
    }),
  ),
  errorMessage: PropTypes.string,
  serverError: PropTypes.string,
  onBlur: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
  onValueChange: PropTypes.func.isRequired,
  onValidationChange: PropTypes.func.isRequired,
  setFieldTouched: PropTypes.func.isRequired,
  value: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.bool,
    PropTypes.number,
    PropTypes.object,
    PropTypes.string,
  ]),
  values: PropTypes.objectOf(PropTypes.any).isRequired,
  resourceType: PropTypes.shape({
    id: PropTypes.string.isRequired,
  }).isRequired,
  meta: PropTypes.shape({
    displayName: PropTypes.string.isRequired,
    readOnly: PropTypes.bool.isRequired,
    dataType: PropTypes.string.isRequired,
    required: PropTypes.bool.isRequired,
    customPresentation: PropTypes.bool.isRequired,
  }).isRequired,
};

EditableFieldV2.defaultProps = {
  value: undefined,
  errorMessage: '',
  serverError: '',
  attributeErrors: undefined,
};

export default EditableFieldV2;
