import React, { Component } from 'react';
import PropTypes from 'prop-types';

import DualColumnSearch from '../DualColumnSearch/DualColumnSearch';
import { PERMISSIONS } from '../../../../utils/permissions/permissions';
import Permission from '../../layout/Permission/Permission';
import { PROP_MODEL_TYPES, MODEL_TYPES } from '../../../../utils/modelTypes';
import { RESOURCE_TYPES } from '../../../../utils/resourceTypes';

class GroupMembershipManagement extends Component {
  constructor(props) {
    super(props);

    this.handleSearch = this.handleSearch.bind(this);
    this.handleClearSearch = this.handleClearSearch.bind(this);

    this.state = {
      isLoadingContent: false,
    };
  }

  componentDidMount() {
    if (this.props.profilePageModel !== MODEL_TYPES.GROUPS) this.handleSearch();
  }

  componentDidUpdate(prevProps) {
    // NOTE: We are ok to use setState here because of the conditional
    // https://reactjs.org/docs/react-component.html#componentdidupdate
    /* eslint-disable react/no-did-update-set-state */
    if (this.props.membershipResults !== prevProps.membershipResults) {
      this.setState({ isLoadingContent: false });
    }
  }

  handleSearch(queryString = '') {
    const { profilePageModel } = this.props;
    this.setState({ isLoadingContent: true }, () => {
      this.props.onSearch(queryString, {
        subquery: true,
        profilePageModel,
      });
    });
  }

  handleClearSearch() {
    const { profilePageModel } = this.props;
    this.props.onClearSearch({
      subquery: true,
      profilePageModel,
    });
  }

  render() {
    const {
      profilePageModel,
      model,
      modelMeta,
      membershipResults,
      modelDisplayAttributeValue,
    } = this.props;
    const { isLoadingContent } = this.state;
    let dropDownProps;

    if (profilePageModel !== MODEL_TYPES.GROUPS) {
      dropDownProps = {
        showDropDown: true,
        dropDownFilter: [RESOURCE_TYPES.GROUP],
        primaryDropDown: false,
      };
    }

    const MembershipManagement = (
      <DualColumnSearch
        profilePageModel={profilePageModel}
        model={model}
        modelMeta={modelMeta}
        modelDisplayAttributeValue={modelDisplayAttributeValue}
        membershipResults={membershipResults}
        onClearSearch={this.handleClearSearch}
        onSearch={this.handleSearch}
        isLoadingSearch={isLoadingContent}
        dropDownProps={dropDownProps}
      />
    );

    if (profilePageModel === MODEL_TYPES.GROUPS) {
      return (
        <Permission
          requiredPermissions={[PERMISSIONS.MANAGE_GROUP_MEMBERSHIP]}
          resourceId={model.type}
          redirect={false}
        >
          {MembershipManagement}
        </Permission>
      );
    }

    return MembershipManagement;
  }
}

GroupMembershipManagement.propTypes = {
  profilePageModel: PropTypes.oneOf(PROP_MODEL_TYPES).isRequired,
  model: PropTypes.shape({
    id: PropTypes.string.isRequired,
    type: PropTypes.string.isRequired,
    attributes: PropTypes.shape({}).isRequired,
    included: PropTypes.arrayOf(PropTypes.shape({})),
  }),
  modelMeta: PropTypes.shape({
    attributes: PropTypes.shape({}).isRequired,
    included: PropTypes.shape({}),
  }),
  modelDisplayAttributeValue: PropTypes.string,
  membershipResults: PropTypes.shape({
    queryString: PropTypes.string.isRequired,
    members: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    nonMembers: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    meta: PropTypes.shape({
      directMembers: PropTypes.shape({}),
      indirectMembers: PropTypes.shape({}),
      nonMembers: PropTypes.shape({}),
    }).isRequired,
    error: PropTypes.string,
    groupsError: PropTypes.bool,
  }),
  onSearch: PropTypes.func.isRequired,
  onClearSearch: PropTypes.func.isRequired,
};

GroupMembershipManagement.defaultProps = {
  model: undefined,
  modelMeta: undefined,
  membershipResults: undefined,
  modelDisplayAttributeValue: undefined,
};

export default GroupMembershipManagement;
