import React from 'react';
import PropTypes from 'prop-types';
import { injectIntl } from 'react-intl';
import _ from 'lodash';
import Spinner from 'ui-library/lib/components/general/Spinner';

import DualColumns from '../DualColumns/DualColumns';
import SearchBar from '../../../search/SearchBar/SearchBar';
import { PROP_MODEL_TYPES } from '../../../../utils/modelTypes';
import intlShape from '../../../../utils/intlPropType';

import './DualColumnSearch.css';

export const DualColumnSearch = (props) => {
  const {
    intl,
    membershipResults,
    onSearch,
    onClearSearch,
    profilePageModel,
    isLoadingSearch,
    dropDownProps,
  } = props;

  return (
    <div className="DualColumnSearch">
      <SearchBar
        className="SearchControlItem input-width-full constant-width"
        onSearch={onSearch}
        onClearSearch={onClearSearch}
        placeholder={intl.formatMessage({
          id: `components.dual-column-search.${profilePageModel}.search-prompt`,
        })}
        queryString={_.get(membershipResults, 'queryString')}
        pageModel={profilePageModel}
        {...dropDownProps}
      />

      <Spinner show={isLoadingSearch}>
        <DualColumns {...props} />
      </Spinner>
    </div>
  );
};

DualColumnSearch.propTypes = {
  intl: intlShape.isRequired,
  profilePageModel: PropTypes.oneOf(PROP_MODEL_TYPES).isRequired,
  model: PropTypes.shape({
    id: PropTypes.string.isRequired,
    attributes: PropTypes.shape({}).isRequired,
    included: PropTypes.arrayOf(PropTypes.shape({})),
  }),
  modelMeta: PropTypes.shape({
    attributes: PropTypes.shape({}).isRequired,
    included: PropTypes.shape({}),
  }),
  membershipResults: PropTypes.shape({
    queryString: PropTypes.string.isRequired,
    members: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    nonMembers: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    meta: PropTypes.shape({
      directMembers: PropTypes.shape({}),
      indirectMembers: PropTypes.shape({}),
      nonMembers: PropTypes.shape({}),
    }).isRequired,
    error: PropTypes.string,
    groupsError: PropTypes.bool,
  }),
  modelDisplayAttributeValue: PropTypes.string,
  queryString: PropTypes.string,
  onClearSearch: PropTypes.func.isRequired,
  onSearch: PropTypes.func.isRequired,
  isLoadingSearch: PropTypes.bool,
  dropDownProps: PropTypes.shape({
    showDropDown: PropTypes.bool,
    dropDownFilter: PropTypes.arrayOf(PropTypes.string),
    primaryDropDown: PropTypes.bool,
  }),
};

DualColumnSearch.defaultProps = {
  model: undefined,
  modelMeta: undefined,
  modelDisplayAttributeValue: undefined,
  membershipResults: undefined,
  queryString: '',
  isLoadingSearch: false,
  dropDownProps: undefined,
};

export default injectIntl(DualColumnSearch);
