import React from 'react';
import _ from 'lodash';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import Icon from 'ui-library/lib/components/general/Icon';

import ColumnLayout from '../../layout/ColumnLayout/ColumnLayout';
import { iconFor, getResourceById } from '../../../../utils/resourceTypes';

import './MembershipSummary.css';

const MembershipSummary = ({
  memberList,
  columnCount,
  columnSize,
}) => {
  // Divide groups into chunks of columnSize and get rid of null entries via filter
  const groupColumns = memberList
    .map((_member, i) => (i % columnSize === 0 && memberList.slice(i, i + columnSize)))
    .filter(member => member);
  const remainingGroupsCount = memberList.length - (columnCount * columnSize);
  const remainingGroupsMessage = (
    <FormattedMessage
      id="components.user-groups-summary.remaining-groups"
      values={{
        count: remainingGroupsCount,
      }}
    />
  );

  return (
    <div className="MembershipSummary">
      <div className="MembershipSummary__title">
        <FormattedMessage
          id="components.user-groups-summary.title"
          values={{
            count: memberList.length,
          }}
        />
      </div>
      <ColumnLayout columns={Math.min(columnCount, groupColumns.length)}>
        {/* eslint-disable react/no-array-index-key */}
        {
          groupColumns.slice(0, columnCount).map((column, index) => (
            <div key={index} className="MembershipSummary__group-column">
              {
                column.map(member => (
                  <div key={member.id} className="MembershipSummary__group">
                    <Icon
                      iconName={iconFor(_.get(getResourceById(member.type), 'attributes.resourceType', ''))}
                      type="leading"
                    />
                    {member.displayName}
                  </div>
                ))
              }
            </div>
          ))
        }
      </ColumnLayout>
      {remainingGroupsCount > 0 && remainingGroupsMessage}
    </div>
  );
};

MembershipSummary.propTypes = {
  memberList: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.string.isRequired,
    displayName: PropTypes.string.isRequired,
    type: PropTypes.string.isRequired,
  })).isRequired,
  columnCount: PropTypes.number.isRequired,
  columnSize: PropTypes.number.isRequired,
};

export default MembershipSummary;
